.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  background-color: #ffffffcc;
  border-bottom: 1px solid #ccc;
  backdrop-filter: blur(5px);
  position: fixed;
  top: 0px;
  z-index: 1;
  width: 100%;
  color: #282c34;
  height: 60px;
}


.navbar-links {
  margin-right: 35px;
}

.navbar a {
  text-decoration: none;
  color: #282c34;
  cursor: pointer;
  margin: 7px;
}